<template>
  <div class="editor-frame--renderer-node-box" @click.stop="handleSelect">
    <div class="editor-frame--renderer-node-box-tools">
      <q-button
        type="text"
        icon="qax-icon-Remove"
        @click.stop="handleDelete"
      ></q-button>
    </div>

    <component :is="node.componentName" v-bind="node.props" :node="node" />
  </div>
</template>



<script >
import { getCurrentInstance, onMounted } from '@vue/composition-api';
import { useDocument } from '../../hooks';
const __sfc_main = {
  name: 'NodeBox'
};
__sfc_main.props = {
  node: {
    type: Object,
    default: () => ({})
  },
  isDragging: {
    type: Boolean,
    default: false
  }
};
__sfc_main.setup = (__props, __ctx) => {
  const props = __props;
  const {
    proxy
  } = getCurrentInstance();
  const document = useDocument();
  const handleSelect = () => {
    if (props.isDragging) return;
    document.value.setSelection(props.node);
  };
  const handleDelete = async () => {
    proxy.$emit('keyCodeDelete', props.node);
  };
  return {
    handleSelect,
    handleDelete
  };
};
export default __sfc_main;
</script>


<style lang="scss" scoped>
.editor-frame--renderer-node-box {
  cursor: move;
  position: relative;
  z-index: 1;
  height: 100%;
}

.editor-frame--renderer-node-box-tools {
  /* display: none; */
  position: absolute;
  right: 8px;
  top: 4px;
  z-index: 1;
  .q-button {
    font-size: 16px;
    color: white;
  }
}

.editor-frame--renderer-node-box:hover .editor-frame--renderer-node-box-tools {
  display: block;
}
</style>
