<template>
  <div class="editor-frame--viewport">
    <q-tabs v-model="activeName">
      <q-tab-pane label="全览视角编辑区" name="map"> </q-tab-pane>
      <q-tab-pane
        v-if="map.props && map.props.mapId"
        label="运营中心视角编辑区"
        name="operate"
      >
      </q-tab-pane>
    </q-tabs>
    <Renderer
      :screen="screen"
      :map="map"
      :nodes="activeName === 'map' ? nodes : operateNodes"
      :isOperate="activeName === 'operate'"
    />
  </div>
</template>



<script >
import { computed } from '@vue/composition-api';
import Renderer from '@/designer/renderer';
import { useDocument } from '@/designer/hooks';
const __sfc_main = {
  name: 'Viewport'
};
__sfc_main.setup = (__props, __ctx) => {
  const document = useDocument();
  const nodes = computed(() => {
    return document.value.nodes.value;
  });
  const operateNodes = computed(() => {
    return document.value.operateNodes.value;
  });
  const map = computed(() => {
    return document.value.map.value;
  });
  const screen = computed(() => {
    return document.value.screen.value;
  });
  const activeName = ref('map');
  return {
    nodes,
    operateNodes,
    map,
    screen,
    activeName
  };
};
__sfc_main.components = Object.assign({
  Renderer
}, __sfc_main.components);
export default __sfc_main;
</script>

<style lang="scss" scoped>
.editor-frame--viewport {
  height: 100%;
  overflow: auto;
  display: flex;
  flex-direction: column;
  ::v-deep .q-tabs {
    flex: none;
  }
}
</style>
