<template>
  <div class="editor-frame--renderer">
    <div class="layout-view" @click="handleSelect('screen')">
      <CanvasBox
        :key="screenSize"
        :w="screenWidth"
        :h="screenHeight"
        :scaleSize="scaleSize / 100"
        @updateScale="updateScale"
        @updateDragging="updateDragging"
        @keyCodeDelete="keyCodeDelete"
      >
        <div class="screen-content">
          <img
            v-if="curMapId"
            :src="require(`@/designer/assets/images/map${curMapId}.png`)"
            alt=""
          />
          <div class="screen-decoration">
            <template v-if="curMapId">
              <img
                v-for="(imgItem, index) in decorationImgList"
                :key="index"
                class="position-img"
                :class="imgItem.position"
                :src="
                  require(`@/designer/assets/images/${column}/${imgItem.bg}.png`)
                "
              />
            </template>
            <div class="position-title">{{ screenTitle }}</div>
            <!-- <div class="title" @click.stop="handleSelect('screen')">
              {{ screenTitle }}
            </div> -->
            <div
              class="grid-stack screen-bi"
              :class="`grid-stack-style${column}`"
              @click.stop="handleSelect('map')"
            >
              <div
                class="grid-stack-item"
                v-for="item in nodesArr"
                :gs-id="item.id"
                :id="item.id"
                :key="item.id"
                :gs-w="item.w"
                :gs-h="item.h"
                :gs-x="item.x"
                :gs-y="item.y"
              >
                <div class="grid-stack-item-content">
                  <NodeBox
                    :node="item"
                    :isDragging="isDragging"
                    @keyCodeDelete="keyCodeDelete"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </CanvasBox>

      <!-- 缩放 -->
      <div
        class="scale-btn"
        :style="`right: ${selection && selection.type ? 380 : 0}px`"
      >
        <span class="prepend-text" @click.stop="handleSize(false)">-</span>
        <span class="scale-text" @click.stop>{{ scaleSize + '%' }}</span>
        <span class="prepend-text" @click.stop="handleSize(true)">+</span>
      </div>
    </div>
  </div>
</template>



<script >
import { getCurrentInstance } from '@vue/composition-api';
import 'gridstack/dist/gridstack.min.css';
import '@/styles/gridstack-column.scss';
import { GridStack } from 'gridstack';
import { useDragon, useWorkbench, useDocument } from '../hooks';
import NodeBox from '../components/node-box';
import { Node, INode } from '../models/node';
import CanvasBox from './components/canvas-box.vue';
import { SCREEN_SIZE } from '@/designer/constants/index';
import DefaultData from './components/defaultData.json';
import { cloneDeep } from 'lodash';
const __sfc_main = {
  name: 'Renderer'
};
__sfc_main.props = {
  nodes: {
    type: Array,
    default: () => []
  },
  map: {
    type: Object,
    default: () => ({})
  },
  screen: {
    type: Object,
    default: () => ({})
  },
  isOperate: {
    type: Boolean,
    default: false
  }
};
__sfc_main.setup = (__props, __ctx) => {
  const props = __props;
  const {
    proxy
  } = getCurrentInstance();
  const dragon = useDragon();
  const workbench = useWorkbench();
  const document = useDocument();
  const nodesArr = ref([]);
  const selection = document.value.selection;
  const decorationImgList = [{
    bg: 'top-bg',
    position: 'position-top'
  }, {
    bg: 'right-bg',
    position: 'position-left'
  }, {
    bg: 'right-bg',
    position: 'position-right'
  }, {
    bg: 'bottom-bg',
    position: 'position-bottom'
  }];
  const assets = computed(() => {
    return workbench.value.assets;
  });
  const curMapId = computed(() => {
    return props.map.props.mapId;
  });
  const screenSize = computed(() => {
    return Number(props.map.props.column);
  });
  const isOperate = computed(() => {
    return props.isOperate;
  });
  const screenTitle = computed(() => {
    return props.map.props.title;
  });
  const isDragging = ref(false);
  const handleSelect = val => {
    // 无主视觉不弹出属性面板
    if (isDragging.value || val === 'map' && !curMapId.value) return;
    document.value.setSelection(props[val]);
  };
  const column = ref(16); // 默认16列
  const screenWidth = ref(1920); // 64为两侧花边 默认宽度1920
  const screenHeight = ref(1080); // 高度固定
  const scaleSize = ref(50); // 默认缩小50%

  const setMap = () => {
    setTimeout(() => {
      grid.value.removeAll();
      document.value.setSchema(cloneDeep(DefaultData));
      document.value.setMapProps('mapId', '2');
      document.value.setMapProps('imageId', 'map2');
      gridInit();
    });
  };
  const setMapConfirm = async _id => {
    // 无主视觉时直接添加
    if (!curMapId.value) {
      setMap();
      return;
    }
    await proxy.$confirm('切换主视觉组件时将清空画布，是否切换？', '切换主视觉组件', {
      type: 'warning'
    }).then(() => {
      document.value.setSelection(null); // 解决属性面板关闭问题
      setMap();
    }).catch(() => {
      document.value.deleteNode(String(_id));
    });
  };
  const grid = ref(null);
  const gridInit = async () => {
    grid.value = GridStack.init({
      acceptWidgets: true,
      marginTop: 0,
      marginBottom: 16,
      marginLeft: 16,
      marginRight: 0,
      disableResize: true,
      float: true,
      row: 10,
      cellHeight: '108px' // 高度以1080px为基准
    });
    nodesArr.value = props.nodes;
    await nextTick();
    grid.value.column(column.value);
    nodesArr.value.forEach(item => {
      grid.value.makeWidget(`#${item.id}`);
    });
    GridStack.setupDragIn('.sidebar .grid-stack-item', {
      appendTo: 'body',
      helper: 'clone'
    });
    grid.value.on('added', async (event, newWidget) => {
      const newWidgetInfo = newWidget[0] || {};
      const componentName = newWidgetInfo.el.innerText;
      if (!componentName.includes('Map')) return;
      setMapConfirm(newWidgetInfo._id);
    });
    grid.value.on('dragstart', function (event, el) {
      isDragging.value = true;
    });

    // 添加小部件
    grid.value.on('dropped', async (event, previousWidget, newWidget) => {
      const {
        w,
        h,
        x,
        y,
        _id
      } = newWidget;
      console.log(x, y, ' x, y,');
      // 兼容多标题
      const componentString = newWidget.el.innerText?.split('\n')[0];
      const componentType = componentString?.split('-')[0];
      const componentName = componentString?.split('-')[1];
      const curComponent = Object.values(assets.value.components).find(item => item.componentName === componentName);
      const {
        props
      } = curComponent.componentMeta;
      const result = {};
      props.forEach(item => {
        result[item.name] = item.defaultValue;
      });
      const addNodeData = {
        id: _id + '',
        componentMeta: curComponent.componentMeta,
        props: result,
        componentName: componentName,
        type: componentType,
        h,
        w,
        x,
        y
      };
      const node = new Node(addNodeData);
      document.value.addNode(node, isOperate.value);
      grid.value.removeWidget(newWidget.el, true, false);
      await nextTick();
      grid.value.makeWidget(`#${_id}`);

      // 拥挤自动布局后更新所有坐标
      const saveItemList = grid.value.save(false);
      saveItemList.forEach(item => {
        const {
          id,
          x,
          y
        } = item;
        document.value.mergeNodeInfo(id, {
          x,
          y
        }, isOperate.value);
      });
    });

    // 拖动坐标更新
    grid.value.on('dragstop', function (event, el) {
      setTimeout(() => {
        isDragging.value = false;
      }, 400);
      const {
        x,
        y,
        id
      } = el.gridstackNode;
      document.value.mergeNodeInfo(id, {
        x,
        y
      }, isOperate.value);
    });
  };

  // 调整大屏比例
  watch(() => screenSize.value, v => {
    column.value = v;
    screenWidth.value = SCREEN_SIZE[v].width;
    document.value.setScreenProps('size', SCREEN_SIZE[v].size);
    // 先清空再赋值 否则尺寸会有影响
    nodesArr.value = [];
    nextTick(() => {
      gridInit();
    });
  }, {
    immediate: true
  });

  // 删除节点后更新
  watch(() => props.nodes, (newVal, oldVal) => {
    if (oldVal.length === 0) return;
    nodesArr.value = newVal;
  }, {
    immediate: true
  });

  // 切换运营中心视角
  watch(() => isOperate.value, () => {
    nodesArr.value = [];
    grid.value.removeAll();
    nextTick(() => {
      gridInit();
    });
  });
  const formatTooltip = val => {
    return `${val}%`;
  };
  const updateDragging = val => {
    isDragging.value = val;
  };
  const handleSize = val => {
    // 最大放大到200%  最小缩小到20%
    if (val && scaleSize.value < 200) {
      scaleSize.value += 10;
      return;
    }
    if (!val && scaleSize.value > 20) {
      scaleSize.value -= 10;
    }
  };
  const updateScale = isAmplify => {
    handleSize(isAmplify);
  };
  const keyCodeDelete = async item => {
    const itemNode = item ? item : selection.value;
    // 只能删除视图、条件组件
    const canDeleteType = ['view', 'condition'];
    if (!itemNode || !canDeleteType.includes(itemNode.type)) return;
    await proxy.$confirm(`是否删除组件：${itemNode.props?.title}`, '删除组件', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    });
    grid.value.removeWidget(itemNode.id, true, false);
    document.value.deleteNode(itemNode.id, props.isOperate);
  };
  return {
    nodesArr,
    selection,
    decorationImgList,
    curMapId,
    screenSize,
    screenTitle,
    isDragging,
    handleSelect,
    column,
    screenWidth,
    screenHeight,
    scaleSize,
    updateDragging,
    handleSize,
    updateScale,
    keyCodeDelete
  };
};
__sfc_main.components = Object.assign({
  CanvasBox,
  NodeBox
}, __sfc_main.components);
export default __sfc_main;
</script>


