      margin-left: 8px;
    }
    .grid-stack-style16 {
      width: calc(100% - 32px);
      margin-left: 8px;
    }
    .grid-stack-style21 {
      width: calc(100% - 42px);
      margin-left: 13px;
    }
    .grid-stack-style32 {
