<template>
  <div class="sidebar">
    <q-tabs v-model="defaultTab">
      <q-tab-pane
        v-for="(item, index) in menuList"
        :key="index"
        :name="String(index)"
        :label="item.title"
        :disabled="item.type !== 'map' && !hasMap ? true : false"
      >
        <q-input
          placeholder="请输入名称搜索"
          prefix-icon="qax-icon-Search"
          size="mini"
          v-model="searchValue"
          class="search-input"
        >
        </q-input>
        <div
          v-for="component in item.components"
          :key="component.componentMeta.name"
          class="grid-stack-item component-item"
          :gs-w="component.componentMeta.w"
          :gs-h="component.componentMeta.h"
        >
          <!-- 拼接参数，拖拽布局需要 -->
          <span class="grid-stack-item-content"
            >{{ component.componentMeta.type }}-{{
              component.componentName
            }}</span
          >
          <span class="item-img">
            <div class="component-img">
              <img
                :title="component.componentName"
                :src="
                  require(`@/designer/assets/images/${component.componentMeta.imgName}.png`)
                "
                alt=""
              />
              <div class="item-id">
                {{ component.componentMeta.componentId }}
              </div>
              <div class="item-size">{{ component.componentMeta.size }}</div>
            </div>

            <div class="item-title">{{ component.componentMeta.title }}</div>
          </span>
        </div>
      </q-tab-pane>
    </q-tabs>
  </div>
</template>



<script >
import { useWorkbench, useDragon, useDocument } from '@/designer/hooks';
import { computed, ref } from '@vue/composition-api';
import { cloneDeep } from 'lodash';
const __sfc_main = {
  name: 'NavMenu'
};
__sfc_main.setup = (__props, __ctx) => {
  const workbench = useWorkbench();
  const dragon = useDragon();
  const document = useDocument();
  const assets = computed(() => {
    return workbench.value.assets;
  });
  const menuList = ref(cloneDeep(assets.value.menu));
  const hasMap = computed(() => {
    return document.value.map.value.props.mapId;
  });
  const defaultTab = ref(0);
  const searchValue = ref('');
  const filterList = () => {
    menuList.value[defaultTab.value].components = cloneDeep(assets.value.menu)[defaultTab.value].components.filter(item => {
      return item.componentMeta.title.includes(searchValue.value);
    });
  };
  watch(() => searchValue.value, () => {
    if (!searchValue.value) {
      menuList.value = cloneDeep(assets.value.menu);
      return;
    }
    filterList();
  });
  return {
    menuList,
    hasMap,
    defaultTab,
    searchValue
  };
};
export default __sfc_main;
</script>

