<template>
  <div class="editor-frame--workbench">
    <div class="editor-frame--workbench-handle">
      <q-button @click="getSchema">保存</q-button>
      <!-- <q-button @click="getJson" style="margin-left: 8px">获取json</q-button> -->
      <q-button @click="preview" style="margin-left: 8px">预览</q-button>
    </div>
    <q-container class="editor-frame--content">
      <q-aside>
        <NavMenu />
      </q-aside>
      <q-main class="editor-frame--workbench-content-panel">
        <Viewport />
      </q-main>
      <!-- <q-aside class="editor-frame--workbench-setter-panel"> -->
      <SetterPanel />
      <!-- </q-aside> -->
    </q-container>
  </div>
</template>



<script >
import NavMenu from './nav-menu';
import Viewport from './viewport';
import SetterPanel from './setter-panel';
import assets from '@/designer/assets/components';
import setters from '@/designer/setters';
import { provide, shallowRef, getCurrentInstance, ref } from '@vue/composition-api';
import createEngine, { EngineContext } from '@/designer/shared/create-engine';
// import DefaultJson from './default.json';
import DefaultJson from '@/designer/renderer/components/defaultData.json';

// 引擎注册
const __sfc_main = {
  name: 'Workbench'
};
__sfc_main.setup = (__props, __ctx) => {
  const engine = shallowRef(null);
  const initEngine = () => {
    engine.value = createEngine({
      assets,
      setters
    });
    engine.value.document.setSchema(DefaultJson);
  };
  initEngine();
  provide(EngineContext, engine);
  const getSchema = () => {
    const schema = engine.value.document.getSchema();
    console.log('schema', schema);
  };
  const getJson = () => {
    console.log(engine.value.document.nodes);
  };
  const {
    proxy
  } = getCurrentInstance();
  const preview = () => {
    const schema = engine.value.document.getSchema();
    // proxy.$router.push({
    //   path: '/preview',
    // });
    window.open('/preview', '_blank');
    localStorage.setItem('schema', JSON.stringify(schema));
  };
  return {
    getSchema,
    preview
  };
};
__sfc_main.components = Object.assign({
  NavMenu,
  Viewport,
  SetterPanel
}, __sfc_main.components);
export default __sfc_main;
</script>

